<template>
  <div>
    <header class="py-5" v-scroll="handleScroll">
      <div class="container d-flex justify-content-between align-items-center flex-wrap">
        <a href="/en/"><img src="../assets/img/abk-logo.svg" alt="" class="logo"></a>
        <span class="d-lg-none hamb-menu" @click="mobileMenu"></span>
        <div class="mainmenu text-lg-left text-center">
          <ul>
            <li><a href="/en/">Home</a></li>
            <li><a href="javascript:;" data-id="aboutus" v-on:click="moveTo">About Us </a></li>
            <li><a href="javascript:;" data-id="location" v-on:click="moveTo">Companies</a></li>
            <li><a href="javascript:;" data-id="news" v-on:click="moveTo">Social Activities & News</a></li>
            <li><a href="javascript:;" data-id="contact" v-on:click="moveTo">Contact</a></li>
          </ul>
        </div>
      </div>
    </header>
    <router-view></router-view>
    <footer>
      <div id="contact">
        <div class="container">
          <div class="row d-flex justify-content-center">
            <div class="col-xl-6 col-lg-9">
              <h2 class="bg-success text-white p-lg-8 p-4">Work with us</h2>
              <div class="form-wrap p-lg-8 p-4 bg-white">
                <form @submit="onSubmit">
                  <div class="d-flex inputGroup flex-wrap">
                    <div>
                      <input class="form-control" type="name" name="name" v-model="form.name" placeholder="Name Surname" required>
                    </div>
                    <div class="ml-md-3 mt-md-0 mt-3">
                      <input class="form-control" type="email" name="email" v-model="form.email" placeholder="E-Mail" required>
                    </div>
                    <div class="mt-3">
                      <input class="form-control" type="tel" name="phone" v-model="form.tel" placeholder="Phone Number" required>
                    </div>
                    <div class="ml-md-3 mt-3">
                      <input class="form-control" type="text" name="subject" v-model="form.subject" placeholder="Subject" required>
                    </div>
                  </div>
                  <button class="btn btn-success w-100 mt-3">Send</button>
                  <div v-if="form.result != null" :class="form.type">{{form.result}}</div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="footer-social py-lg-16 py-10 d-flex flex-column align-items-center">
        <img src="../assets/img/ft-abk-logo.svg" alt="" width="130">
        <div class="social-media mt-lg-10 mt-6">
          <a href="#" class="fc"></a>
          <a href="#" class="tw"></a>
          <a href="#" class="yt"></a>
          <a href="#" class="pt"></a>
        </div>
      </div>
      <div class="footer-bottom py-lg-5 py-3 text-center">
        <div class="container">
          <p> 2020 ABK</p>
        </div>
      </div>
    </footer>
  </div>
</template>

<script>     
  import axios from 'axios';
  export default {
    data(){
      return{
        form: {
              name: null,
              email: null,
              tel: null,
              subject: null,
              result:null,
              type:""
          }
      }
    },
    methods: {
      moveTo: function (e) {
        const link = $(".mainmenu").find("a");
        link.removeClass("active");
        e.target.classList.add("active");
        const dataId = e.target.getAttribute('data-id');
        const sectionID =  document.getElementById(dataId);
        const headerWrap = document.querySelector("header");
        document.querySelector(".mainmenu").classList.remove('opened');
        document.querySelector(".hamb-menu").classList.remove('opened');
        if ( sectionID === null){
          this.$router.push("/").then(() => {
            const activeDataId = e.target.getAttribute('data-id');
            const activeSectionID =  document.getElementById(activeDataId);
            $("html, body").stop(true, false).animate({scrollTop: activeSectionID.offsetTop - 100,}, 700);
          });  
        }else{
          const topVal = sectionID.offsetTop - 100;
          $("html, body").stop(true, false).animate({scrollTop: topVal}, 700);

        }
      },
      handleScroll: function(evt, el) {
        if (window.scrollY > 50) {
          el.classList.add("min");
        }else{
          el.classList.remove("min");
        }
        const menuItem = $(".mainmenu li").children("a");
        menuItem.each(function() {
          const id = $("#"+$(this).attr("data-id"));
          if ( id.length > 0 ){
            $(this).attr("data-top", id.offset().top - 104 );
            if ( window.scrollY > $(this).attr("data-top") ){
              menuItem.removeClass('active');
              $(this).addClass('active');
            }else if( window.scrollY === 0 ){
              menuItem.removeClass('active');
            }
          }
        }); 

      },
      mobileMenu: function( event ) {
        const menu =  document.querySelector(".mainmenu")
        event.target.classList.toggle('opened');
        menu.classList.toggle('opened');
      },
      onSubmit: function (e) {  
            const vm = this;
            axios({
                method: 'post',
                url: '/send_email.php',
                data: {
                  'name': this.form.name,
                  'email': this.form.email,
                  'tel': this.form.tel,
                  'subject': this.form.subject,
                },
            }).then(response => {
                this.form.result = response.data.message;
                this.form.type = 'alert mt-3 alert-'+response.data.type;  
            }).catch(error => {
                console.log(error.message)
            }).then(function () {
              const inputs = document.querySelectorAll(".form-control");
              for(var i = 0; inputs.length > i; i++){
                inputs[i].value = "";
              }
              setTimeout(function(){
                vm.form.name = null;
                vm.form.email = null;
                vm.form.tel = null;
                vm.form.subject = null;
                vm.form.result = null;
                vm.form.type = "";
              }, 4000);
            });
            this.form.result = "Loading to email.";
            this.form.type = 'alert mt-3 alert-info';
            e.preventDefault();
        }
    }
  }
</script>



